import React from 'react';
import Card from './Card';


const CardList = ({ cars, onCarClick }) => {
	return (
	    <div>
	      {
		    cars.map((user, id) => {
				return (
					<Card
					  key={id}
					  id={cars[id].id}
					  manufacturer={cars[id].manufacturer}
					  model={cars[id].model}
					  onCarClick={onCarClick}
					  />
					)
			    }
			)
		  }
	    </div>
	);
}


export default CardList;