import React from 'react';

const Card = ({ manufacturer, model, id, onCarClick }) => {
	return (
		<div className='tc bg-light-red dib br3 pa3 ma2 grow bw2 shadow-5 pointer' onClick={onCarClick}>
			<img className='h5' alt={`${id}`} src={`img/${manufacturer}/${id}.jpeg`} />
			<h2>{manufacturer}</h2>
			<p>{model}</p>
		</div>
	)
}

export default Card;