import React from 'react';
import Manufacturer from './Manufacturer';


const SearchBox = ({ searchfield, searchChange, cars }) => {
	return (
		<div>
	      {
		    cars.map((user, id) => {
				return (
					<Manufacturer
					  key={id}
					  searchChange={searchChange}
					  manufacturer={cars[id].manufacturer}
					  />
					)
			    }
			)
		  }
	    </div>
	);
}

export default SearchBox;