// import logo from './logo.svg';
import React, { Component } from 'react';
import './App.css';
import SearchBox from '../components/SearchBox';
import Scroll from '../components/Scroll';
import CardList from '../components/CardList';
import Car from '../components/Car';
import cars from './cars.json'

class App extends Component {

  constructor() {
		super();
		this.state = {
      cars: cars,
      searchfield: [],
      car_id_to_render: ''
		}
	}

  onSearchChange = (event) => {
    var array = [...this.state.searchfield];

    if (event.target.checked) {
      array.push(event.target.value)
      this.setState({ searchfield: array });
    } else {
      var index = array.indexOf(event.target.value)
      array.splice(index, 1);
      this.setState({searchfield: array});
    }
  }
  
  onCarClick = (event) => {
    this.setState({ car_id_to_render: event.target.alt });
  }

  UndoOnCarClick = (event) => {
    this.setState({ car_id_to_render: '' });
  }

  render() {

    const { cars, searchfield } = this.state;
    const CarId = this.state.car_id_to_render
		const filteredCars = cars.filter(car => {
      if (searchfield.length === 0) {
        return car.manufacturer;
      } else {
        return searchfield.includes(car.manufacturer.toLowerCase())
      }
    })
    const filterdCarById = cars.filter(car => {
      return car.id.toString() === CarId
    })
    
    return (
      <div className='tc'>
        <h1 className='f1'>NaDaAuto</h1>
        <div className='light-red'>
          <p>Комиссионная продажа авто. Кредитование. Лизинг. Срочный выкуп</p>
          <p>Улица Уборевича 95. Автостоянка №32.</p>
          <p>Евгений +375(29)2325636</p>
        </div>
        <div className='fl w-20 pa2 bt b--white'>
          <SearchBox searchChange={this.onSearchChange} cars={cars}/>
        </div>
          
        <div className='tl fl w-80 pa2 bt bl b--white'>
          <Scroll>
            {CarId === ''
              ? <CardList cars={filteredCars} onCarClick={this.onCarClick}/>
              : <Car cars={filterdCarById} CarId={CarId} onCarClick={this.UndoOnCarClick}/>
            }
            
          </Scroll>
        </div>
      </div>
    );
  }
}

export default App;
